<script>
import AnalyzeGraph from './components/Analyze.vue';
import OverviewGraph from './components/Overview.vue';
// import TableComponent from "@/components/Table.vue";
export default {
  name: 'App',
  components: {
    AnalyzeGraph,
    OverviewGraph
    // TableComponent
},  
  data () {
    return {
      activeIndex: '1',
      activeName: 'OverviewTab',
      option4Overview: {},
      option4Analyze: {},
      tableData: [],
      MaxBlocknumber: 15365903,
      checked: true,
      value1: true,
      ignore: true,
      // default_icon_url: "https://raw.githubusercontent.com/Liuyushiii/img/master/unknown.png",
      icon_url: [""],
      MinBlocknumber: 15265903,
      jump: 0,
      token: 'Eth',
      blockThreshold: 7200,
      value: 5,
      realTime: new Date().toLocaleString(),
      accounts: ['0xc611952d81e4ecbd17c8f963123dec5d7bce1c274'],
      contracts: [""],
      blockNumber: 16540597,
                txTimer: undefined,
                blockTime: 'Unknown',
                dashboard: {
                    "hour": 0,
                    "minute": 0,
                    "seconds": 0
                }
    }
  },
  created () {
    setInterval(()=>{
      let time = new Date();
      this.realTime = time.toLocaleString();
      let startTime = "2022/6/23 0:00:00";
      let distance = time.getTime() - new Date(startTime).getTime();
      this.dashboard.hour = parseInt(distance / 3600000);
      this.dashboard.minute = parseInt(distance % 3600000 / 60000);
      this.dashboard.seconds = parseInt(distance % 60000 / 1000);
      // this.get_icon_url()
    }, 1000)
  },
  mounted () {
  },
  methods: {
    default_icon_url: function(event, index) {
      this.icon_url[index] = "https://raw.githubusercontent.com/Liuyushiii/img/master/unknown.png"
    },
    get_this_icon_url:function(index) {
      return this.icon_url[index]
    },
    get_icon_url: function(i) {
      // for (var i = 0; i < this.contracts.length; i++) {
        var fill_in = this.contracts[i];
        if (fill_in == "" || fill_in == undefined) fill_in = ""
        // console.log(fill_in)
        if (fill_in == "") {
          this.icon_url[i] = "https://raw.githubusercontent.com/Liuyushiii/img/master/unknown.png"
          return
        }
        this.$http.get(`https://raw.githubusercontent.com/Liuyushiii/img/master/${this.contracts[i]}.png`).then(function (res) {
                    this.icon_url[i] = `https://raw.githubusercontent.com/Liuyushiii/img/master/${this.contracts[i]}.png`
                }, function (err) {
                  this.icon_url[i] = "https://raw.githubusercontent.com/Liuyushiii/img/master/unknown.png"
                })
      // }
    },
    getInputStyle: function() {
      if (this.accounts.length > 1) {
        return "width: 170px; margin-right: 10px"
      }
      return "width: 200px" 
    },
    getcontractsInputStyle: function() {
      if (this.contracts.length > 1) {
        return "width: 110px; margin-left: 10px;margin-right: 10px"
      }
      return "width: 160px; margin-left: 10px" 
    },
    addContract: function(event) {
      this.contracts.push("")
      this.icon_url.push("https://raw.githubusercontent.com/Liuyushiii/img/master/unknown.png")
    },
    deleteContract: function(event, index){
      if (this.contracts.length <= 1) {
        return
      }
      this.contracts.splice(index, 1)
    }, 
    addAccount: function(event) {
      this.accounts.push("0xc611952d81e4ecbd17c8f963123dec5d7bce1c274")
    },
    deleteAccount: function(event, index) {
      if (this.accounts.length <= 1) {
        return 
      }
      this.accounts.splice(index, 1)
    },
    changeChart: function() {
      this.$refs.AnalyzeGraph.changeAnalyzeGraph(this.option4Analyze)
      this.$refs.OverviewGraph.changeOverviewGraph(this.option4Overview)
    },
    search: function () {
      this.$http.post('/data/analyze/', {type:"query", address: this.accounts, khop: this.jump, start_blk: this.MinBlocknumber, end_blk: this.MaxBlocknumber, contracts: this.contracts, timeInterval: this.blockThreshold, valueDifference: this.value, ignore: this.ignore, eth: this.checked}, {headers: {'Content-Type': 'application/json'}}, {emulateJSON: true}).then(function (res) {
                    // this.token = res.body.token
                    this.code = 200
                    // this.activeNumber = 3
                    this.option4Analyze = {
                      "nodes": res.body.analyze.nodes,
                      "edges": res.body.analyze.edges,
                    }
                    this.$refs.AnalyzeGraph.changeAnalyzeGraph(this.option4Analyze)
                    this.option4Overview = {
                      "nodes": res.body.overview.nodes,
                      "edges": res.body.overview.edges,
                      "json": res.body.overview.json
                    }
                    this.$refs.OverviewGraph.changeOverviewGraph(this.option4Overview)
                }, function (err) {
                    this.code = err.status
                    this.message = err.body.message
                })
      // this.$http.post('/data/overview/', {type:"query", address: this.accounts, khop: this.jump, start_blk: this.MinBlocknumber, end_blk: this.MaxBlocknumber, contracts: this.contracts, timeInterval: this.blockThreshold, valueDifference: this.value}, {headers: {'Content-Type': 'application/json'}}, {emulateJSON: true}).then(function (res) {
      //     // this.token = res.body.token
      //     this.code = 200
      //     this.option4Overview = {
      //       "nodes": res.body.nodes,
      //       "edges": res.body.edges,
      //       "json": res.body.json
      //     }
      //     this.$refs.OverviewGraph.changeOverviewGraph(this.option4Overview)
      // }, function (err) {
      //     this.code = err.status
      //     this.message = err.body.message
      // })
      // this.changeChart()
    }
  }
}
</script>
