<template>
    <div style="width: 100%">
      <!-- <div class="choice" v-show="!empty">
        <div class="each-choice" style="background: lightgray;border-bottom: 1px solid white;">
          <img src="https://raw.githubusercontent.com/Liuyushiii/img/master/ETH.png"><span style="font-size: small; font-weight: 700; margin-left: 10px">ETH</span>
        </div>
        <div class="each-choice" style="background: lightgray">
          <img src="https://raw.githubusercontent.com/Liuyushiii/img/master/USDC.png"><span style="font-size: small; font-weight: 700; margin-left: 7.5px">USDC</span>
        </div>
      </div> -->
        <el-empty description="No Data" style="width: 100%" v-show="empty"></el-empty>
        <div id="analyze" style="width: 1200px;height: 800px;" v-show="!empty"></div>
    </div>
</template>
<!-- <script type="text/javascript" src="/static/js/echarts.js"></script> -->
<script>
    export default {
        name: "AnalyzeGraph",
        props: ['option'],
        data() {
            return {
                empty: true,
                chart: undefined,
            }
        },
        mounted() {
        },
        // watch: {

        //     option(newOption) {
        //         console.log(newOption)
        //         this.empty = false
        //         this.Draw(newOption["nodes"], newOption["edges"], newOption["dic"])
        //     }

        //     },
        methods: {
          changeAnalyzeGraph: function(newOption) {
            console.log(newOption)
            this.empty = false
            this.Draw(newOption["nodes"], newOption["edges"])
            // this.pie(newOption["json"])
          },
            Draw: function (nodes, edges) {
              if (this.chart != undefined) {
                    this.chart.destroy()
                }
                if (this.graph != undefined) {
                  this.graph.destroy()
                  this.graph = undefined
                }
                this.nodesNumber = nodes.length
                this.edgesNumber = edges.length
                const tooltip = new this.$antv.Tooltip({
    offsetX: 10,
    offsetY: 10,
    // the types of items that allow the tooltip show up
    // 允许出现 tooltip 的 item 类型
    itemTypes: ['node', 'edge'],
    // custom the tooltip's content
    // 自定义 tooltip 内容
    getContent: (e) => {
      const outDiv = document.createElement('div');
      // outDiv.style.width = 'fit-content';
      outDiv.style.padding = '0px 40px 0px 20px';
      if (e.item.getType() == 'node'){
        outDiv.innerHTML = `
        <h4>Account Information</h4>
        <ul>
            <li>Address: ${e.item.getModel().id}</li>
        </ul>`;
      }
      else if (e.item.getType() == 'edge'){
        outDiv.innerHTML = `
        <h4>Transaction Information</h4>
        <ul>
          <li>From: ${e.item.getModel().source}</li>
          <li>To: ${e.item.getModel().target}</li>
          <li>Value: ${e.item.getModel().value}</li>
        </ul>
        `;
      }
      return outDiv;
    },
    shouldBegin: (e) => {
      // console.log(e.target);
      let res = true;
      switch (e.item.getModel().id) {
        case '1':
          res = false;
          break;
        case '2':
          if (e.target.get('name') === 'text-shape') res = true;
          else res = false;
          break;
        case '3':
          if (e.target.get('name') !== 'text-shape') res = true;
          else res = false;
          break;
        default:
          res = true;
          break;
      }
      return res;
    },
  });
  const container = document.getElementById('analyze');
        // 配置容器属性
        const width = 1200;
        const height = 800;
        const default_node_size = 30;
        const color_list = [
        '#9EC9FF',
        '#5AC8A6',
   //     '#5D7092',
        '#F6C816',
        '#E8684A',
        '#6DC8EC',
        '#9270CA',
        '#FF9D4D',
        '#269A99',
        '#FF99C3',
        ];
        const graph = new this.$antv.Graph({
            container: 'analyze',
            width,
            height,
            // 布局
            layout: {
                type: 'dagre',
                rankdir: 'LR', // 可选，默认为图的中心
                align: undefined, // 可选
                nodesep: 50, // 可选
                ranksep: 50, // 可选
                controlPoints: true, // 可选
              },
            // 默认节点大小
            defaultNode: {
                type: 'circle',
                size: default_node_size,
                // color: 'black',
                style: {
                    fill: '#9EC9FF',
                    lineWidth: 1,
                    stroke: ''
                  },
                labelCfg: {
                  position: 'top',
                  style:{
                    fontSize: 15,
                    fontWeight: 700
                  }
                }
            },
            defaultEdge: {
                //type: 'quadratic',
                style: {
                  lineWidth: 1.5,
                  endArrow: true,
                },
                labelCfg: {
                  autoRotate: true,
                },
            },
            // 配置插件
            plugins: [tooltip],
            modes: {
                default: ['drag-canvas', 'drag-node', 'zoom-canvas'],
              },
        });
        
        function get_degree(degree){
            if (degree < 10){
              return default_node_size + 2 * degree
            }
            return default_node_size + 1 * degree
        }

        function get_label(id, degree){
          if (degree > 5){
            var firstChars = id.substring(0,5)
            var lastChars = id.substring(id.length-3)
            return firstChars + '...' + lastChars
          }
          return ''
        }

        function get_color(degree){
          if (degree >= 5 && degree <= 10){
            return color_list[1]
          }
          else if (degree > 10){
            return color_list[2]
          }
          return color_list[0]
        }

        nodes.forEach(node=>{
          // 配置节点名称
          var id = node.id;
          // 配置节点大小
          var degree = node.degree
          // node.size = get_degree(degree)
          node.cluster = 0
          node.label = get_label(id, degree)
          node.style ={
            fill: get_color(degree)
          }
        });
        graph.data({
          nodes: nodes,
          edges: edges
          });
        // this.$antv.Util.processParallelEdges(data.edges);

        graph.render();

        graph.on('node:click', (evt) => {
          const { item } = evt;
          item.toFront();
          // graph.setItemState(item, 'selected', true);
        });
        this.graph = graph
        // label 过长截断 显示...
  }
        },
        // mounted() {
        // }
    }
</script>
<style>
.choice {
  position: absolute;
  left: 10px;
  width: 75px;
  height: 57.5px;
  border-left: 1px solid lightgray;
  border-right: 1px solid lightgray;
  border-top: 1px solid lightgray;
}
.each-choice {
  width: 100%;
  height: 28.75px;
  border-bottom: 1px solid lightgray;
  line-height: 28.75px;
  display: flex;
  align-items: center;
  text-align: center;
}
.each-choice img {
  width: 20px;
  margin-left: 7.5px;
}
</style>